

































































import { Vue, Component, Watch } from "vue-property-decorator";
import LedgerTable from "@/microApps/businessObjectManage/views/ledger/components/LedgerTable.vue";
import PropertyDetails from "@/microApps/businessObjectManage/views/ledger/components/PropertyDetails.vue";
import api from "@/microApps/businessObjectManage/api";
import utils from "@/utils/utils";

@Component({
  components: {
    PropertyDetails,
    LedgerTable
  }
})
export default class Ledger extends Vue {
  // 类型树信息
  treeMessage: any = {
    // 过滤条件
    filterText: "",
    // tree数据
    data: [],
    // 配置选项
    defaultProps: {
      children: "children",
      label: "baseTypeName"
    }
  };
  // 选中表单信息
  basicForm: any = {};
  value1: Array<any> = [];
  isShow: boolean = true;
  method: string = "add";
  selectedObject: any = {};
  organizationTree: any[] = []; // 筛选条件：行政区域数据

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.tree as any).filter(val);
  }

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.treeMessage.defaultProps.label].indexOf(value) !== -1;
  }

  // 树节点点击事件
  handleNodeClick(data: any) {
    this.basicForm = Object.assign({}, data);
    switch (this.basicForm.code) {
      case "car":
        this.$set(this.basicForm, "baseTypeCodeName", "car");
        break;
      case "person":
        this.$set(this.basicForm, "baseTypeCodeName", "person");
        break;
      case "building":
        this.$set(this.basicForm, "baseTypeCodeName", "building");
        break;
      case "device":
        this.$set(this.basicForm, "baseTypeCodeName", "device");
        break;
      default:
        this.$set(this.basicForm, "baseTypeCodeName", "general");
    }
    this.$nextTick(() => {
      if (this.$refs.tree) {
        (this.$refs.tree as any).setCurrentKey(data.id);
      }
    });
  }
  // 获取基础业务对象类型
  async getBaseTypeList() {
    let {
      data: { status, message, data }
    } = await this.$http.get(api.base.getObjectBaseType);
    if (status) {
      this.treeMessage.data = data;
      this.handleNodeClick(data[0]);
    } else {
      this.$message.error(message);
    }
  }
  // 获取组织机构树
  async getOrganization() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.ledger.getOrganizationTree);
    if (status) {
      // this.organizationTree = data;
      utils.removeEmptyChildren(data);
      this.$set(this, "organizationTree", data);
    } else {
      // this.$message.error(message)
    }
  }

  activeSon(method: string, data: any) {
    this.isShow = !this.isShow;
    this.method = method;
    this.selectedObject = data;
  }

  created() {
    this.getOrganization();
    this.getBaseTypeList();
  }
}
