
























































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "@/microApps/businessObjectManage/api";
import BindObjectTable from "@/microApps/businessObjectManage/views/ledger/components/BindObjectTable.vue";
import ObjectGeneralForm from "@/microApps/businessObjectManage/views/ledger/objectForm/ObjectGeneralForm.vue";
import ObjectCarForm from "@/microApps/businessObjectManage/views/ledger/objectForm/ObjectCarForm.vue";
import ObjectBuildingForm from "@/microApps/businessObjectManage/views/ledger/objectForm/ObjectBuildingForm.vue";
import ObjectDeviceForm from "@/microApps/businessObjectManage/views/ledger/objectForm/ObjectDeviceForm.vue";
import ObjectPersonForm from "@/microApps/businessObjectManage/views/ledger/objectForm/ObjectPersonForm.vue";
import utils from "@/utils/utils";

@Component({
  components: { ObjectGeneralForm, BindObjectTable, ObjectCarForm, ObjectBuildingForm, ObjectDeviceForm, ObjectPersonForm }
})
export default class PropertyDetails extends Vue {
  @Prop({ type: String, default: "add" })
  method!: string;
  @Prop({ type: String })
  id!: string;
  @Prop({ type: Object })
  baseType!: any;
  @Prop({ type: Boolean })
  displayState!: boolean;
  @Prop({ type: Array })
  organizationTree!: any;

  detailTypeTree: any = []; // 当前选中大类下的子类型树
  objectForm: any = {
    baseTypeId: this.baseType.id,
    extInfo: []
  };
  // 级联统一配置
  commonProps: any = {
    checkStrictly: true,
    emitPath: false,
    label: "name",
    value: "id"
  };
  isDisabled: boolean = this.method !== "edit";
  organizations: any[] = [];
  whichComponent: string = "ObjectGeneralForm";

  @Watch("organizationTree", { immediate: true, deep: true })
  handleChangeOrg(val: any) {
    if (val && val.length) {
      this.$set(this, "organizations", val);
    }
  }

  created() {
    this.formDisplay(this.baseType.baseTypeCodeName);
    this.getTypeTree(this.baseType.id);
    if (this.method === "add") {
      this.isDisabled = true;
      this.objectForm = {
        baseTypeId: this.baseType.id,
        extInfo: []
      };
      return;
    }
    if (this.id) {
      this.getObjectDetail(this.id).then(() => {
        this.changeFormContent(this.objectForm.typeDetailId);
      });
      return;
    }
  }
  // 根据类型展示表单
  formDisplay(code: string) {
    switch (code) {
      case "car":
        this.whichComponent = "ObjectCarForm";
        break;
      case "person":
        this.whichComponent = "ObjectPersonForm";
        break;
      case "building":
        this.whichComponent = "ObjectBuildingForm";
        break;
      case "device":
        this.whichComponent = "ObjectDeviceForm";
        break;
      default:
        this.whichComponent = "ObjectGeneralForm";
        break;
    }
  }
  // 获取小类型类型树
  async getTypeTree(id: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.ledger.getObjectTypeTree + "/" + id);
    if (status) {
      if (data && data.length) {
        utils.removeEmptyChildren(data);
        this.detailTypeTree = data;
      } else {
        this.$message.error(`${this.baseType.baseTypeName}下无可用类型创建实例`);
      }
    }
  }
  // 获取类型对应详细信息
  async changeFormContent(id: string) {
    try {
      this.isDisabled = this.method === "preview";
      let {
        data: { data, status, message }
      } = await this.$http.get(api.baseType.getTypeDetailInfo + id);
      if (status) {
        if (this.method === "add") {
          this.$set(this.objectForm, "extInfo", data.extList || []);
        }
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      console.log(e);
    }
  }
  // 获得组件CustomInput填充之后的数据
  async getFilledCustomOptions() {
    let customInput: any = this.$refs["objectExt"];
    if (customInput) {
      return await customInput.formValidation();
    }
    return null;
  }
  // 获取实例详情
  async getObjectDetail(id: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get((api.ledger.getDetail as any)[this.baseType.baseTypeCodeName] + id);
    if (status) {
      this.objectForm = data;
      // this.objectForm = Object.assign({ extInfo: data.extInfoVO ? data.extInfoVO : [] }, data);
      if (!this.objectForm.extInfo) {
        this.$set(this.objectForm, "extInfo", data.extInfoVO || []);
      }
    } else {
      this.$message.error(message);
    }
  }
  // 保存实例对象
  async saveObject() {
    let valid: any = await (this.$refs["objectFormRef"] as any).formValidate();
    if (valid) {
      let extData = await this.getFilledCustomOptions();
      let url: string = "";
      if (this.method === "add") {
        url = (api.ledger.add as any)[this.baseType.baseTypeCodeName];
      } else {
        url = (api.ledger.update as any)[this.baseType.baseTypeCodeName];
      }
      this.$http
        .post(url, {
          ...this.objectForm,
          extInfo: extData
        })
        .then((res: any) => {
          if (res.data.status) {
            this.$message.success("保存成功！");
            this.$emit("callFather");
          } else {
            this.$message.error(res.data.message);
          }
        });
    }
  }

  backToMain() {
    this.$emit("callFather");
  }
}
