
































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "@/microApps/businessObjectManage/api";

@Component({})
export default class PropertyDetails extends Vue {
  @Prop({ type: String })
  objectId!: string;

  searchVO: any = {
    id: this.objectId,
    keyword: ""
  };

  bindObjectList: any[] = []; // 已绑定对象

  created() {
    this.getAllBindList();
  }
  // 获取已绑定实例
  async getAllBindList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.objectDetail.getAllBind, { params: { ...this.searchVO } });
    if (status) {
      this.bindObjectList = data;
    } else {
      this.$message.error(message);
    }
  }

  handleCurrentChange(num: number) {
    this.searchVO.pageIndex = num;
    this.getAllBindList();
  }
}
