









































































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "@/microApps/businessObjectManage/api";
import utils from "@/utils/utils";

@Component({})
export default class LedgerTable extends Vue {
  @Prop({ type: String })
  baseType!: string;
  @Prop({ type: String })
  typeCode!: string;
  @Prop({ type: Boolean })
  displayState!: boolean;
  @Prop({ type: Array })
  organizationTree!: any;

  // 分页与筛选
  searchVO: any = {
    current: 0,
    query: {
      location: [],
      name: "",
      orgId: "",
      typeDetailId: "",
      typeId: ""
    },
    size: 10
  };
  commonProps: any = {
    checkStrictly: true,
    emitPath: false,
    label: "name",
    value: "id"
  }; // 级联统一配置
  total: number = 0;
  tableData: any[] = [];
  detailTypeTree: any[] = []; // 筛选条件：业务类型数据（根据左侧大类动态获取）
  organizations: any[] = [];
  typeObject: { [key: string]: any } = {}; // 保存获取到的具体类型数据
  isLoading: boolean = true;
  filterStatus: boolean = false;
  dialogVisible: boolean = false;

  // 监听业务大类id，查询对应小类类型书与对象实例列表
  @Watch("baseType")
  handleChangeTypeTree(val: any) {
    if (val && val.length) {
      this.searchVO = {
        current: 1,
        query: {
          location: [],
          name: "",
          orgId: "",
          typeDetailId: "",
          typeId: val
        },
        size: 10
      };
      this.getTypeTree(val);
      this.getObjectList();
    } else {
      this.tableData = [];
      this.total = 0;
    }
  }
  @Watch("displayState", { immediate: true })
  handleChangeStatus(val: boolean) {
    if (val && this.baseType) {
      this.getObjectList();
    }
  }
  @Watch("organizationTree", { immediate: true, deep: true })
  handleChangeOrg(val: any) {
    if (val && val.length) {
      this.$set(this, "organizations", val);
    }
  }
  // 获取小类型类型树
  async getTypeTree(id: string) {
    if (this.typeObject[this.baseType]) {
      this.detailTypeTree = this.typeObject[this.baseType];
    } else {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.ledger.getObjectTypeTree + "/" + id);
      if (status) {
        this.$set(this.typeObject, this.baseType, data);
        this.detailTypeTree = data;
      } else {
        // this.$message.error(message)
      }
    }
  }
  // 获取对象实例列表
  async getObjectList() {
    try {
      this.isLoading = true;
      let url: string = (api.ledger.getList as any)[this.typeCode] || api.ledger.getList.general;
      let {
        data: { data, status, message }
      } = await this.$http.post(url, this.searchVO);
      if (status) {
        this.tableData = data.records;
        this.total = data.total;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.isLoading = false;
    }
  }
  // 删除实例
  async deleteTheObject(item: any) {
    console.log(item);
    let url: string = (api.ledger.delete as any)[this.typeCode] || api.ledger.delete.general;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(url + item.id);
      if (status) {
        this.$message.success("删除成功！");
        this.search();
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      console.log(e);
    }
  }
  // 根据筛选条件查询，重置页数
  search() {
    this.searchVO.current = 1;
    this.total = 0;
    if (this.filterStatus) this.filterStatus = false;
    this.getObjectList();
  }
  handleCurrentChange(page: number) {
    this.searchVO.current = page;
    this.getObjectList();
  }

  switchToDetails(method: string, data?: any) {
    this.$emit("callFather", method, data || { id: "" });
  }
}
