





















































































































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import Utils from "@/utils/utils";
import { ElUpload } from "element-ui/types/upload";
import api from "@/microApps/businessObjectManage/api";

const AMap: any = (window as any).AMap;
const AMapUI: any = (window as any).AMapUI;

@Component({})
export default class ObjectDeviceForm extends Vue {
  @Prop({ type: Array })
  organizationTree!: any;
  @Prop({ type: Object })
  value!: any;
  @Prop({ type: Boolean, default: false })
  disabled!: boolean;

  objectChildForm: any = this.value ? this.value : {};
  objectChildFormRules: any = {
    // code: { required: true, trigger: "change", message: "该选项为必填项" },
    name: { required: true, trigger: "change", message: "该选项为必填项" },
    // display: { required: true, trigger: "change", message: "展示形式不能为空" },
    orgId: { required: true, trigger: "change", message: "该选项为必填项" }
  };
  // 当前用户能看到的所有组织
  orgOptions: Array<any> = [];
  // 设备归类选项
  classifyOptions: Array<any> = [];
  // 地理位置选择
  map: any;
  marker: any = null;
  addressDialog = false;
  text = "建筑物坐标绑定";
  showLeaveChoose = false;
  zoom = 18;
  center = [106.569137124949, 29.643814122592];
  dialogVisible: boolean = false; // 定位选择弹窗状态
  hasImage: boolean = false;

  created() {
    this.getOrganizations();
    this.getClassifyTree();
  }
  // 获取组织类型
  getOrganizations() {
    this.$http.get(api.interfaceDevice.getNewOrgTree).then((response: any) => {
      if (response.data.status) {
        this.orgOptions = response.data.data;
        // this.$nextTick(() => {
        //   (this.$refs.orgTree as ElTree).setCheckedKeys([this.vehicleDetailsForm.orgId]);
        // });
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 获取设备归类
  getClassifyTree() {
    this.$http.get(api.interfaceDevice.classifyTree).then((res: any) => {
      if (res.data.status) {
        this.classifyOptions = res.data.data;
        // this.$nextTick(() => {
        //   let deviceClassifyIds = this.objectChildForm.deviceClassifyIds;
        //   deviceClassifyIds && (this.$refs.classTree as ElTree).setCheckedKeys(deviceClassifyIds);
        // });
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  // 绑定地理信息
  bindAddress() {
    this.addressDialog = true;
    this.$nextTick(() => {
      // 已绑定
      if (this.objectChildForm.location) {
        let lnglat = typeof this.objectChildForm.location === "string" ? JSON.parse(this.objectChildForm.location) : this.objectChildForm.location;
        this.initMap(lnglat); // 将地图中心调整为 该建筑坐标
        this.addBuildMarker(lnglat, this.objectChildForm.address); // 在地图上显示该点
        this.showLeaveChoose = false;
        this.text = "当前点位绑定坐标为：" + this.objectChildForm.location;
      }
      // 未绑定
      else {
        this.text = "请在地图上选取建筑物位置，并双击确定！";
        // 调用地图交互事件
        this.initMap(this.center);
        this.chooseSiteOnMap();
      }
    });
  }

  // 载入地图
  initMap(center: any) {
    const _this = this;
    this.map = new AMap.Map("deviceAmap", {
      center: center,
      zoom: this.zoom,
      features: ["bg", "point", "road", "building"]
    });
    AMapUI.loadUI(["misc/PoiPicker"], function(PoiPicker: any) {
      var poiPicker = new PoiPicker({
        input: "deviceInput"
      });
      //初始化poiPicker
      _this.poiPickerReady(poiPicker);
    });
  }
  poiPickerReady(poiPicker: any) {
    const _this = this;
    (window as any).poiPicker = poiPicker;
    let marker = new AMap.Marker();
    // let infoWindow = new AMap.InfoWindow({
    //   offset: new AMap.Pixel(0, -20)
    // });
    // 选取了某个POI
    poiPicker.on("poiPicked", function(poiResult: any) {
      let source = poiResult.source;
      let poi = poiResult.item;
      let info = {
        source: source,
        id: poi.id,
        name: poi.name,
        location: poi.location.toString(),
        address: poi.formattedAddress
      };
      marker.setMap(_this.map);
      // infoWindow.setMap(_this.map);
      marker.setPosition(poi.location);
      // infoWindow.setPosition(poi.location);
      // // infoWindow.setContent("POI信息: <pre>" + JSON.stringify(info, null, 2) + "</pre>");
      // infoWindow.open(_this.map, marker.getPosition());
      _this.map.setCenter(marker.getPosition());
    });
  }
  // 地图上显示marker标点
  addBuildMarker(position: any, address: string) {
    let _this = this;
    this.marker = new AMap.Marker({
      position: position
    });
    this.marker.setLabel({
      //修改label相对于maker的位置
      offset: new AMap.Pixel(20, -20),
      content: "<div style='margin: 2px 5px; font-family: PingFang SC, sans-serif;line-height: 16px'>" + address + "</div>"
    });
    this.marker.setMap(this.map);
  }
  // 结束坐标选取，重新载入地图
  chooseOff() {
    this.text = "建筑物坐标绑定";
    this.showLeaveChoose = false;
    this.initMap(this.center);
  }
  // 开始地图交互事件
  chooseSiteOnMap() {
    let _this = this;
    this.showLeaveChoose = true;
    this.text = "请在地图上选取建筑物位置，并双击确定！";
    this.map.clearMap();
    // 修改鼠标样式( 十字型 )
    this.map.setDefaultCursor("crosshair");
    this.map.on("dblclick", function(e: any) {
      let lnglat = [e.lnglat.getLng(), e.lnglat.getLat()];
      _this.text = "您选取了 " + JSON.stringify(lnglat) + " 的位置！";
      let geocoder = new AMap.Geocoder({
        radius: 1000,
        extensions: "all"
      });
      geocoder.getAddress(lnglat, function(status: any, result: any) {
        if (status === "complete" && result.info === "OK") {
          let address = result.regeocode.formattedAddress; //返回地址描述
          _this
            .$prompt("当前选中位置：", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              inputValue: address
            })
            .then((obj: any) => {
              _this.objectChildForm.location = JSON.stringify(lnglat);
              _this.objectChildForm.address = obj.value;
              _this.addressDialog = false;
            })
            .catch(() => {
              // _this.$message.info("请重新选择");
              _this.text = "请在地图上选取建筑物位置，并双击确定！";
            });
        }
      });
    });
  }
  // 销毁地图
  destroyMap() {
    this.map.destroy();
  }
  // 自定义上传文件
  customHttpRequest(data: any) {
    let fData = Utils.createUploadForm(data.file);
    this.$http.post(data.action, fData.formData, fData.config).then((response: any) => {
      if (response.data.status) {
        this.hasImage = true;
        // this.objectChildForm.image = response.data.data.accessUrl;
        this.$set(this.objectChildForm, "image", response.data.data.accessUrl);
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  handleRemove() {
    this.objectChildForm.image = "";
    this.hasImage = false;
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }

  beforeAvatarUpload(file: any) {
    const isJPG = file.type === "image/jpeg" || file.type === "image/png" || file.type === "image/jpg"; // image/jpeg,image/png,image/jpg
    const isLt5M = file.size / 1024 / 1024 < 5;
    if (!isJPG) {
      this.$message.error("只能上传jpg、png及下属格式的图片");
    }
    if (!isLt5M) {
      this.$message.error("上传图片大小不能超过 5MB!");
    }
    return isJPG && isLt5M;
  }

  @Watch("objectChildForm", { immediate: true, deep: true })
  handleReturnData() {
    this.$emit("input", this.objectChildForm);
    this.$emit("change", this.objectChildForm);
  }

  @Watch("value", { immediate: true, deep: true })
  handleChangeVal() {
    this.$set(this, "objectChildForm", this.value);
  }

  formValidate() {
    return new Promise((resolve, reject) => {
      let formRef: any = this.$refs["objectChildFormRef"];
      formRef.validate((valid: boolean) => {
        if (valid) {
          resolve(true);
        } else {
          reject(false);
        }
      });
    });
  }
}
